.main-container {
	padding: 40px 40px;
}

.form-container {
	padding: 20px;
	background: #FEFDF9;
	border: solid 1px #C3C2C2;
	border-radius: 6px;
}

.lead {
	margin-bottom: 10px;
}
section section {
	margin-bottom: 10px;
}

.article-area {
	border: solid 1px #C8C8C8;
	background: #fff;
	padding: 30px;
	border-radius: 6px;
}

.main-visual {
	padding: 100px 40px 60px;
	color: #fff;
	background: #9e897d;
	background-image: linear-gradient(
		-45deg,
		rgba(80,60,30,.3) 25%,
		rgba(80,60,30,.5) 25%, rgba(80,60,30,.5) 50%,
		rgba(80,60,30,.3) 50%, rgba(80,60,30,.3) 75%,
		rgba(80,60,30,.5) 75%, rgba(80,60,30,.5)
	);
	background-size: 50px 50px;

	a {
		color: #fff;
		text-decoration: underline;
	}

}

.navbar .nav-top-icon {
	font-size: 18px;
}

footer {
	text-align: center;
	padding: 20px 0;
	border-top: 1px solid #C6C2BA;
	position: relative;

	&:after {
		position: absolute;
		content: "";
		width: 100%;
		height: 1px;
		display: block;
		background: #fff;
		top: 1px;
	}

	a {
		color: #4A4A4A;
	}
}

